.NjvVFG_diff ul {
  margin: 0;
  padding-inline-start: calc(1.4375em + 2px);
  position: relative;
}

.NjvVFG_diff ul ul {
  margin-top: .25em;
}

.NjvVFG_diff ul ul:before {
  content: "";
  position: absolute;
  top: .1875em;
  bottom: 1em;
  left: 0;
}

.NjvVFG_diff .NjvVFG_node {
  padding: .1875em 0;
  line-height: 1em;
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_hover > ul:before {
  z-index: 1;
  border-left: 1px solid #ddd;
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_object, .NjvVFG_diff .NjvVFG_node.NjvVFG_array {
  list-style-type: disclosure-open;
  position: relative;
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_object.NjvVFG_collapsed, .NjvVFG_diff .NjvVFG_node.NjvVFG_array.NjvVFG_collapsed {
  list-style-type: disclosure-closed;
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_object.NjvVFG_collapsed > ul, .NjvVFG_diff .NjvVFG_node.NjvVFG_array.NjvVFG_collapsed > ul {
  display: none;
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_object.NjvVFG_collapsed > .NjvVFG_property:after, .NjvVFG_diff .NjvVFG_node.NjvVFG_array.NjvVFG_collapsed > .NjvVFG_property:after {
  content: "";
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_leaf {
  align-items: flex-start;
  column-gap: .5em;
  display: flex;
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_leaf:before {
  content: "";
  width: 1em;
  height: .5em;
  color: #ddd;
  display: block;
  position: absolute;
  left: 0;
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_leaf:hover:before {
  border-bottom: 1px solid;
  border-left: 1px solid;
}

.NjvVFG_diff .NjvVFG_node > .NjvVFG_type {
  text-transform: uppercase;
  color: #fff;
  border-radius: 3px;
  padding: 2px 4px;
  font-size: .75em;
  font-weight: bold;
  transform: translateY(-2px);
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_removed > .NjvVFG_type {
  background-color: red;
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_added > .NjvVFG_type {
  background-color: green;
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_new > .NjvVFG_type {
  background-color: #00f;
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_unchanged > .NjvVFG_type {
  background-color: gray;
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_updated > .NjvVFG_type {
  background-color: #ff8c00;
}

.NjvVFG_diff .NjvVFG_node > .NjvVFG_property {
  color: gray;
  font-style: italic;
}

.NjvVFG_diff .NjvVFG_node > .NjvVFG_property:after {
  content: ":";
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_array > ul > .NjvVFG_node > .NjvVFG_property {
  width: 1em;
  height: 1em;
  text-align: center;
  border: 1px solid;
  border-radius: 50%;
  flex-shrink: 0;
  padding: .125em;
  font-size: .75em;
  font-style: normal;
  line-height: 1em;
  display: inline-block;
}

.NjvVFG_diff .NjvVFG_node.NjvVFG_array > ul > .NjvVFG_node > .NjvVFG_property:after {
  content: "";
}

.NjvVFG_diff .NjvVFG_node > .NjvVFG_value > .NjvVFG_content {
  font-size: .9375em;
}

.NjvVFG_diff .NjvVFG_node > .NjvVFG_value.NjvVFG_old > .NjvVFG_content {
  text-decoration: line-through;
}

.NjvVFG_diff .NjvVFG_node > .NjvVFG_value.NjvVFG_new > .NjvVFG_content {
  color: #00f;
}

.NjvVFG_diff .NjvVFG_node > .NjvVFG_value > .NjvVFG_list:before {
  content: "[ ";
}

.NjvVFG_diff .NjvVFG_node > .NjvVFG_value > .NjvVFG_list:after {
  content: " ]";
}

.NjvVFG_diff .NjvVFG_node > .NjvVFG_value > .NjvVFG_list > span + span:before {
  content: ", ";
}

.NjvVFG_diff .NjvVFG_node > .NjvVFG_toggle-handler {
  height: 1.125em;
  cursor: pointer;
  border-radius: 2px;
  margin: .125em 0;
  padding: .125em 0;
  display: block;
  position: absolute;
  top: -.125em;
  left: calc(-1.4375em - 2px);
  right: 0;
}

.NjvVFG_diff .NjvVFG_node > .NjvVFG_toggle-handler:hover:after {
  content: attr(data-hint);
  color: #888;
  font-size: .65em;
  position: absolute;
  right: 0;
}

.NjvVFG_diff .NjvVFG_node .NjvVFG_placeholder {
  color: #fff;
  text-transform: uppercase;
  border-radius: 4px;
  padding: 0 4px;
  font-family: monospace;
  font-size: .7em;
  font-weight: 600;
}

.NjvVFG_diff .NjvVFG_node .NjvVFG_placeholder.NjvVFG_empty, .NjvVFG_diff .NjvVFG_node .NjvVFG_placeholder.NjvVFG_truncated {
  background-color: #ccc;
}

.NjvVFG_diff .NjvVFG_node .NjvVFG_placeholder.NjvVFG_object {
  background-color: #222;
}

.NjvVFG_preview {
  z-index: 1;
  background: #fff;
  padding: .5em 1em;
  font-size: .8em;
  display: none;
  position: absolute;
  transform: translateY(-1.5em);
  box-shadow: 0 0 8px 4px #00000026;
}

.NjvVFG_preview:hover, .NjvVFG_object.NjvVFG_placeholder:hover + .NjvVFG_preview {
  display: block;
}

